body {
  margin: 0;
  padding: 0;
  font-family: 'Fira Code';
  font-size: 12px;
  color: #666;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  background-color: #f5f7f9;
}

.me {
  position: fixed;
  bottom: 35px;
  right: 50px;
}
.me .avatar {
  display: block;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.2);
}

.dashboard {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.move {
  margin-top: -10vh;
}
.progress-container {
  font-size: 18px;
  position: relative;
}
.progressbar-title {
  font-family: 'PT Sans', sans-serif;
  margin-top: 25px;
  position: relative;
}
.progressbar-title h3 {
  padding: 0;
  margin: 0;
  display: inline-block;
}
.progressbar-title .percent {
  font-family: 'Operator Mono';
  position: absolute;
  bottom: 0;
  right: 5px;
}
.bar-container {
  background: rgba(85, 85, 85, 0.8);
  position: relative;
  height: 6px;
  width: 500px;
  margin-top: 5px;
  display: block;
  box-shadow: 0 2px 8px 0 rgba(99, 99, 99, 0.5);
  border-radius: 6px;
}
.bar-container .progressbar {
  position: absolute;
  height: 100%;
  width: 0%;
  border-radius: 6px;
}

